.tile-content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap; /* Allow images to wrap if there are multiple */
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.scaled-image {
  max-width: 50%;
  max-height: 50%;
  transition: transform 0.3s ease-in-out;
  margin: 2px; /* Add spacing between images */
}

.expanded .scaled-image {
  transform: scale(1.5);
}

.active {
  background-color: #f0f0f0; /* Highlight clicked date */
}

