/* Apply scaling to images within modal */
.modal-content .modal-image {
  max-width: 50%; /* You can adjust this value as needed */
  max-height: 50vh; /* You can adjust this value as needed */
  margin: 10px;
}

.modal-image-container {
  position: relative;
}

.event-link,
.view-flyer-link {
  display: block;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  text-decoration: none;
  font-size: 12px;
  margin-top: 5px;
}
